<script>
import pmt from 'formula-pmt'
// import plans from 'assets/plans.json'
export default {
  data () {
    return {
      periodList: [
        {
          value: 36,
          label: '36'
        },
        {
          value: 24,
          label: '24'
        },
        {
          value: 18,
          label: '18'
        },
        {
          value: 12,
          label: '12'
        }
      ],

      period: 36,
      total: 0,
      percentRate: 8.8,
      plan1firstRate: 0.2,
      plan1financeRate: 0.8,
      plan1finalRate: 0,
      plan2firstRate: 0.3,
      plan2financeRate: 0.7,
      plan2finalRate: 0.3,
      plan3FirstRate: 30,
      plan3financeRate: '',
      plan3FinalRate: 0
      // plans:plans
    }
  },
  computed: {
    rate: function () {
      return this.percentRate / 100
    },
    plan1: function () {
      let _firstPayment = this.plan1firstRate * this.total
      let _financePayment = this.plan1financeRate * this.total
      let _finalPayment = this.plan1finalRate * this.total
      let _perMonth = pmt(this.rate / 12, this.period, -_financePayment, _finalPayment)
      let _interest = (_perMonth * this.period - _financePayment + _finalPayment) / 36 / 30
      let _feeRate = 0
      if (_financePayment !== 0) {
        _feeRate = (_perMonth * this.period - _financePayment + _finalPayment) / _financePayment / 3
      }
      return {
        firstRate: (this.plan1firstRate * 100).toFixed(2),
        firstPayment: _firstPayment.toFixed(2),
        financeRate: (this.plan1financeRate * 100).toFixed(2),
        financePayment: _financePayment.toFixed(2),
        finalRate: (this.plan1finalRate * 100).toFixed(2),
        finalPayment: _finalPayment.toFixed(2),
        perMonth: _perMonth.toFixed(2),
        interest: _interest.toFixed(2),
        feeRate: (_feeRate * 100).toFixed(2)
      }
    },

    plan2: function () {
      let _firstPayment = this.plan2firstRate * this.total
      let _financePayment = this.plan2financeRate * this.total
      let _finalPayment = this.plan2finalRate * this.total
      let _perMonth = pmt(this.rate / 12, this.period, -_financePayment, _finalPayment)
      let _interest = (_perMonth * this.period - _financePayment + _finalPayment) / 36 / 30
      let _feeRate = 0
      if (_financePayment !== 0) {
        _feeRate = (_perMonth * this.period - _financePayment + _finalPayment) / _financePayment / 3
      }
      return {
        firstRate: (this.plan2firstRate * 100).toFixed(2),
        firstPayment: _firstPayment.toFixed(2),
        financeRate: (this.plan2financeRate * 100).toFixed(2),
        financePayment: _financePayment.toFixed(2),
        finalRate: (this.plan2finalRate * 100).toFixed(2),
        finalPayment: _finalPayment.toFixed(2),
        perMonth: _perMonth.toFixed(2),
        interest: _interest.toFixed(2),
        feeRate: (_feeRate * 100).toFixed(2)
      }
    },
    plan3firstRate: function () {
        return this.plan3FirstRate / 100
    },
    plan3finalRate: function () {
        return this.plan3FinalRate / 100
    },
    plan3: function () {
      this.plan3financeRate = 1 - this.plan3firstRate
      let _firstPayment = this.plan3firstRate * this.total
      let _financePayment = this.plan3financeRate * this.total
      let _finalPayment = this.plan3finalRate * this.total
      let _perMonth = pmt(this.rate / 12, this.period, -_financePayment, _finalPayment)
      let _interest = (_perMonth * this.period - _financePayment + _finalPayment) / 36 / 30
      let _feeRate = 0
      if (_financePayment !== 0) {
        _feeRate = (_perMonth * this.period - _financePayment + _finalPayment) / _financePayment / 3
      }
      return {
        firstRate: (this.plan3firstRate * 100).toFixed(2),
        firstPayment: _firstPayment.toFixed(2),
        financeRate: (this.plan3financeRate * 100).toFixed(2),
        financePayment: _financePayment.toFixed(2),
        finalRate: (this.plan3finalRate * 100).toFixed(2),
        finalPayment: _finalPayment.toFixed(2),
        perMonth: _perMonth.toFixed(2),
        interest: _interest.toFixed(2),
        feeRate: (_feeRate * 100).toFixed(2)
      }
    }

    // plan: function (num) {
